.TodoItem {
  width: 320px;
  background-color: #fff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 10px rgba(32, 35, 41, 0.15);
  border-radius: 8px;
  height: auto;
}

.TodoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
}

.TodoItem-p--complete {
  text-decoration: line-through;
  color: #878887;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  position: absolute;
  left: 12px;
}

.Icon-check--active {
  color: #4caf50;
}

.Icon-delete {
  position: absolute;
  top: -24px;
  right: 0;
  font-size: 38px;
}
.Icon-delete:hover {
  color: red;
}
